import {
  Component, EventEmitter, OnInit,
  Output
} from '@angular/core';
import { RouterModule } from '@angular/router';
import { UserIp } from '@biz-nest/auth-client';
import { FrontendLogger, IfilterProps, IuserIpAnalytics } from '@biz-nest/core-client';
import {
  EventbusService, TooltipService
} from '@front-shared-lib';

@Component({
  selector: 'app-user-ips-analytics',
  standalone: true,
  imports: [
    RouterModule
  ],
  templateUrl: './user-ips-analytics.component.html'
})
export class UserIpsAnalyticsComponent
implements OnInit {
  @Output() filterBy = new EventEmitter<IfilterProps>();
  total = 0;
  analyticsResult?: IuserIpAnalytics;

  constructor(
    public eventbus: EventbusService,
    public tooltipService: TooltipService
  ) {}

  ngOnInit(): void {
    this.getUserIpAnalytics();
  }

  async getUserIpAnalytics(
    month = new Date().getMonth() + 1,
    year = new Date().getFullYear()
  ) {
    try {
      const beginDate = new Date(year, month - 1, 1);
      const endDate = new Date(year, month, 0);

      const filterProps: IfilterProps = {
        comparisonFilter: [{
          field: 'createdAt',
          fieldValue: beginDate,
          operator: 'gte'
        }, {
          field: 'createdAt',
          fieldValue: endDate,
          operator: 'lte'
        }]
      };

      this.analyticsResult = await UserIp.getUserIpAnalytics(filterProps);
    } catch (error) {
      FrontendLogger.error(error);
    }
  }
}

